import React from "react";
import "./Movie.css";
import PropTypes from "prop-types";
import LinesEllipsis from "react-lines-ellipsis";

class Movie extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      maxLine: 3,
      useEllipsis: true
    };
    this._onTextClick = this._onTextClick.bind(this);
  }

  _onTextClick(e) {
    const prevState = this.state.useEllipsis;
    this.setState({ useEllipsis: !prevState });
  }

  render() {
    return (
      <div className="Movie">
        <div className="Movie_Column">
          <MoviePoster poster={this.props.src} alt={this.props.title} />
        </div>
        <div className="Movie_Column">
          <h1>{this.props.title}</h1>
          <div className="Movie_Genres">
            {this.props.genres.map((genre, index) => (
              <MovieGenre onclick={this.props.onClickGenre} key={index} genre={genre} />
            ))}
          </div>
          <div onClick={this._onTextClick} className="Movie_Synopsis">
            {this.state.useEllipsis ? (
              <LinesEllipsis
                text={this.props.synopsis}
                maxLine={this.state.maxLine}
                ellipsis="<Read More>"
                trimRight
                basedOn="letters"
              />
            ) : <div>{this.props.synopsis} </div>}
          </div>
        </div>
      </div>
    );
  }
}

function MovieGenre({ genre, onclick }) {
  return <span className="Movie_Genre" onClick={()=>onclick({genre})}>{genre}</span>;
}
function MoviePoster({ poster, alt }) {
  return <img src={poster} alt={alt} title={alt} className="Movie_Poster" />;
}
Movie.propTypes = {
  title: PropTypes.string.isRequired,
  src: PropTypes.string.isRequired,
  genres: PropTypes.array,
  synopsis: PropTypes.string.isRequired
};
MoviePoster.propTypes = {
  poster: PropTypes.string.isRequired,
  alt: PropTypes.string.isRequired
};
MovieGenre.propTypes = {
  genre: PropTypes.string.isRequired
};
export default Movie;



